.drag-drop{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 2rem;
    font-size: 1.5rem;
    color: #999;
    border-radius: 5px;
    cursor: pointer;
}

.drag-drop p{
    margin: 0 0 1rem;
}

.custom-file-upload{
    padding: .5rem 1rem;
    font-weight: 600;
    color: white;
    background-color: #3897f0;
    border: #3897f0;
    cursor: pointer;
}

.custom-file-upload:hover{
    background-color: #2684f0;
    border-color: #2684f0;
}

input[type="file"]{
    display: none;
}